@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&display=swap');

*{
  margin:0;
  padding:0;
  box-sizing:border-box;
}

body{
  background-color: rgb(204, 199, 199);
}

.heading{
  font-family: sans-serif;
  font-weight: 700;
  text-align: center;
  padding: 15px 0px;
  background-color: whitesmoke;
  color: rgb(109, 109, 109);
  
}


.list{
  padding: 50px 40px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}

.card{
  background-color: whitesmoke;
  width: 18rem;
  margin: 20px;
  border-radius: 15px;
  transition: 0.4s ease-in-out;
}

.card:hover{
  transform: scale(1.01);
  box-shadow: 0px 7px 10px rgba(0,0,0,0.4);
  transition: 0.4s ease-in-out;
}

.card-img img{
height: 200px;
width: 18rem;
border-radius: 15px 15px 0 0 ;
}

.card-title{
  text-align: center;
  font-family: sans-serif;
  color: rgb(27, 27, 27);
  font-size: 18px;
  margin: 10px;
  font-weight: 600;
}

.card a{
  cursor: default;
  text-decoration: none;
}
.but{
  border: none;
  cursor: pointer;
  display: block;
  background-color: rgb(184, 12, 12);
  color: white;
  text-decoration: none;
  padding: 7px;
  border-radius: 5px;
  margin: 7px auto 10px;
}